    var elmnt;

    if (scrollTo === 'previous') {
      if (this.currentSlide <= 0) return;
      this.currentSlide -= 1;      
      elmnt = document.getElementById(this.currentSlide);
    }

    else if (scrollTo === 'next') {
      if (this.currentSlide >= 3) return;
      this.currentSlide += 1;
      elmnt = document.getElementById(this.currentSlide);
    }

    else {
      elmnt = document.getElementById(scrollTo);
    }

